* {
  margin: 0;
  padding: 0;
}
.class-a-title {
  color: #262729;
  font-size: 20px;
  font-weight: bold;
  text-align: left;
  letter-spacing: 1px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
}
.class-a-title-tips {
  display: block;
  font-size: 16px;
  color: #A5A9AF;
  text-align: left;
  line-height: 25px;
  letter-spacing: 0.5px;
  margin-top: 9px;
  margin-right: 0;
  margin-bottom: 0;
  margin-left: 0;
}
.classes-or-grade {
  position: relative;
  width: 240px;
  height: 56px;
  line-height: 56px;
  border-radius: 8px;
  margin-top: 36px;
  margin-right: 0;
  margin-bottom: 36px;
  margin-left: 0;
  border: 1px solid #0dc2b3;
}
.classes-or-grade > span {
  font-size: 14px;
  color: #0dc2b3;
  float: left;
  display: inline-block;
  width: 50%;
  height: 100%;
  text-align: center;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  cursor: pointer;
}
.classes-or-grade > span:hover {
  opacity: 0.8;
}
.classes-or-grade .current {
  color: #fff;
  background: #0dc2b3;
}
.class-b-container {
  margin-top: 0;
  margin-right: 0;
  margin-bottom: 50px;
  margin-left: 0;
  text-align: left;
}
.class-b-icon {
  display: inline-block;
  width: 15px;
  height: 15px;
  vertical-align: middle;
  border-radius: 50%;
  background: #0dc2b3;
}
.class-b-title {
  font-size: 14px;
  font-weight: 500;
